/* App.css */

body {
  margin: 0;
  margin-top: 4%;
  font-family: Arial, sans-serif;
}

.app-container {
  display: flex;
  height: 100vh;
  position: relative;
}

/* Mobile Menu Button */
.mobile-menu-button {
  display: none;
  position: fixed;
  top: 15px;
  left: 15px;
  z-index: 1000;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
}

.mobile-menu-button span {
  display: block;
  width: 20px;
  height: 2px;
  background-color: #12161B;
  margin: 4px 0;
  transition: 0.4s;
}

.sidebar-container {
  width: 25%;
  background-color: #ffffff;
  transition: transform 0.3s ease-in-out;
}

.sidebar {
  width: 100%;
  background-color: #ffffff;
  color: #7f8c8d;
  padding: 20px;
  box-sizing: border-box;
  font-family: 'Playfair Display', serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  margin-top: 2%;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.sidebar li {
  text-align: right;
}

.sidebar a {
  text-decoration: none;
  color: #7f8c8d;
  font-size: 20px;
  font-weight: 550;
  padding: 3px 15px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.nav-icon {
  width: 22px;
  height: 22px;
}

.social-icon {
  width: 20px;
  height: 20px;
}

.social-links {
  display: flex;
  justify-content: right;
  margin-top: 2%;
  gap: 15px;
}

.social-links a {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Social media icon colors */
.social-links a:nth-child(1) .social-icon {
  color: #333; /* GitHub */
}

.social-links a:nth-child(2) .social-icon {
  color: #1DA1F2; /* Twitter */
}

.social-links a:nth-child(3) .social-icon {
  color: #0077B5; /* LinkedIn */
}

.social-links a:nth-child(4) .social-icon {
  color: #000000; /* Medium */
}

/* Mobile Styles */
@media (max-width: 768px) {
  body {
    margin-top: 0;
  }

  .mobile-menu-button {
    display: block;
  }

  .sidebar-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 45px;
    transform: translateX(-100%);
    z-index: 999;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out, width 0.3s ease-in-out;
  }

  .sidebar-container.open {
    transform: translateX(0);
    width: 160px;
  }

  .content {
    width: 100%;
    margin-left: 0;
    padding: 15px;
    transition: margin-left 0.3s ease-in-out;
  }

  .content.shifted {
    margin-left: 160px;
  }

  .sidebar {
    padding: 50px 5px 20px 5px;
    margin-top: 0;
    overflow: hidden;
    height: 100%;
  }

  .sidebar ul {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .sidebar li {
    text-align: left;
    margin: 0;
  }

  .sidebar a {
    font-size: 16px;
    padding: 8px 10px;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 15px;
    white-space: nowrap;
  }

  .nav-text {
    display: none;
  }

  .sidebar-container.open .nav-text {
    display: block;
  }

  .social-links-container {
    margin-top: auto;
    padding-top: 20px;
  }

  .social-links {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    margin: 0;
    padding-left: 12px;
  }

  .social-links a {
    padding: 0;
  }

  .social-icon {
    width: 22px;
    height: 22px;
  }

  .back-button {
    padding: 8px;
    margin-bottom: 20px;
  }
}

.sidebar a.active-link {
  font-weight: bold;
  color: #12161B;
  font-weight: 550;
}

.content {
  width: 75%;
  padding: 20px;
  box-sizing: border-box;
  overflow-y: auto;
  transition: margin-left 0.3s ease-in-out;
}

.social-links {
  display: flex;
  justify-content: right; /* Align items to the start */
  margin-top: 2%; /* Space above the icons */
}

.social-links a {
  margin-right: 10px; /* Reduced space between icons */
  text-decoration: none; /* Remove underline */
  padding: 0; /* Remove any default padding */
  font-size: 20px; /* Default font size for social icons */
}

/* Media query for mobile devices */
@media (max-width: 768px) {
  .social-links a {
    font-size: 16px; /* Smaller font size for mobile */
    margin-right: 8px; /* Reduce margin for mobile */
  }
}


.social-links a:last-child {
  margin-right: 15px; /* Remove margin from the last icon */
}

.social-links a:hover {
  opacity: 0.7; /* Slightly fade on hover */
}

.about-me h2 {
  color: #12161B; /* Match sidebar link color */
  font-family: 'Playfair Display';
  margin-top: 4%;
  margin-bottom: 35px;
  font-weight: 550;
  font-size: 56px;
}

.about-me p {
  font-size: 16px; /* Adjust paragraph size */
  font-family: 'Open Sans', sans-serif; /* Ensure paragraphs use Open Sans */
  line-height: 1.6; /* Improve readability with line height */
  font-weight: 400;
  margin-right: 28%;
  color: #6d7070; 
}

.blog-detail {
  margin-left: 2%;
}
.spaced-text {
  word-spacing: 2px; /* Adjust this value to increase or decrease space */
}

.writing h2 {
  color: #12161B; /* Match sidebar link color */
  font-family: 'Playfair Display';
  margin-top: 4%;
  margin-right: 5%;
  margin-bottom: 45px;
  font-weight: 550;
  font-size: 56px;
}

.blog-item hr {
  border: none; /* Remove default border */
  border-top: 1px solid #aeb7b7; /* Add a light gray line */
  margin: 20px 0; /* Space above and below the line */
  margin-right: 45%;

}

.blog-item {
  margin-bottom: 20px; /* Space between blog items */
}

.blog-title {
  font-size: 22px; /* Title font size */
  margin-bottom: 5px;
  font-family: 'Playfair Display';
  color: #16191e; /* Match sidebar link color */
  font-weight: 450;
}

.blog-item h4 {
  margin-top: 0px;
  font-size: 15px; /* Subheading font size */
  color: #6d7070;  /* Lighter color for subheading */
  font-family: 'Open Sans', sans-serif; /* Ensure paragraphs use Open Sans */
  font-weight: lighter;
}

.blog-item p {
  font-size: 12px; /* Adjust paragraph size to match About Me */
  font-family: 'Open Sans', sans-serif; /* Ensure paragraphs use Open Sans */
  line-height: 1.6; /* Improve readability with line height */
  font-weight: 400;
  margin-right: 5px;
  color: #6d7070; /* Text color */
  display: inline; /* Ensure paragraph is inline to avoid line breaks */
}

.read-more {
  display: inline-block; /* Ensure proper spacing */
  text-decoration: none; /* Remove underline */
  color: #6d7070; /* Text color */
  margin-left: 15px; /* Adjust percentage as needed */
  font-size: 12px;
}


.blog-subheading {
  margin-bottom: 5px; /* Reduced space between subheading and published date */
}

.published-date {
  font-size: 5px; /* Font size for published date */
  
}

.writing li {
  list-style-type: none;
}

.writing ul {
  padding-left: 5px;
}

.back-button {
  color: #12161B;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: transparent;
  padding-right: 15px;
  padding-bottom: 150px;
  padding-top: 0px;
  font-weight: 150;
}


.published-date-detail {
  font-size: 12px; 
  margin-top: 0px;
  color: #6d7070; /* Text color */
  padding-left: 0px;
  padding-bottom: 30px;
}

.blog-detail h2 {
  font-size: 52px; /* Title font size */
  margin-bottom: 5px;
  margin-right: 20%;
  font-family: 'Playfair Display';
  color: #16191e; /* Match sidebar link color */
  font-weight: 850;
}


.markdown-content {
  margin-right: 20%; /* Add right side margin */
  line-height: 1.6; 
  font-family: 'Open Sans', sans-serif; /* Ensure paragraphs use Open Sans */
  font-weight: 500;
  margin-right: 20%;
  color: #6d7070; 
}

.markdown-content a {
  color: black; /* Set link color to black */
  text-decoration: none; /* Remove underline */
  text-decoration: underline; /* Underline on hover for better visibility */
}

.markdown-content h1,
.markdown-content h2,
.markdown-content h3,
.markdown-content h4,
.markdown-content h5,
.markdown-content h6 {
  color: #2c3e50; /* Darker color for headings */
}

.markdown-content p {
  margin-bottom: 15px; /* Space between paragraphs */
}

.markdown-content img {
  max-width: 100%; /* Responsive images */
  height: auto; /* Maintain aspect ratio */
  border-radius: 5px; /* Optional: rounded corners */
}


.home h2 {
  margin-right: 20%; /* Add right side margin */
  line-height: 1.6; 
  font-family: 'Open Sans', sans-serif; /* Ensure paragraphs use Open Sans */
  font-weight: 500;
  margin-right: 20%;
  color: #6d7070; 
  padding-left: 10px;
}





/* Tag styles */
.blog-tags {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.blog-tags .tag {
  background-color: #f2f2f2;
  color: #333;
  font-size: 8px;
  padding: 5px 10px;
  border-radius: 15px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.blog-tags .tag:hover {
  background-color: #12161B;
  color: #fff;
}

/* Tags filter container */
.tags-container {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.tags-container button {
  background: transparent;
  border: 1px solid #ccc;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.tags-container button.active {
  background-color: #12161B;
  color: white;
  border-color: #12161B;
}

.tags-container button:hover {
  background-color: #eaeaea;
}


.blog-title-link {
  text-decoration: none; /* Remove underline */
  color: inherit; /* Keep the same text color */
}

